.cont-figure {
  background-color: #f2f2f2;
}

.cont-figure .figure {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.cont-figure .figure:hover {
  opacity: 1;
}

.cont-figure .figure .picture {
  padding-top: calc((2 / 3)* 100%);
}

.cont-figure .figure .content-video {
  width: 100%;
  background: #f2f2f2;
  max-height: 100%;
  max-width: 100%;
  position: relative;
}

.cont-figure .figure .content-video > * {
